import React from 'react'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

function Posts() {

    const [posts, setPosts] = useState([])
    let likesToggle = false
    let dislikesToggle = false

    useEffect(() => {
        axios.get('/posts-api/posts/').then(res => {
            setPosts(res.data)
            // console.log(posts)
        })
    }, [])

     const setLikesHandler = async (id, btn) => {
        likesToggle = !likesToggle
        let post = await (await axios.get(`/posts-api/posts/${id}/`)).data
        console.log(post)

        likesToggle ? 
        await axios.patch(`/posts-api/posts/${id}/`, {likes: post.likes + 1}): 
        await axios.patch(`/posts-api/posts/${id}/`, {likes: post.likes - 1})

        // setPosts([...posts, posts[id - 1].likes = post.likes + 1])

        likesToggle ?
        btn.children[0].style.color = 'red':
        btn.children[0].style.color = 'black'
 
    }

    const setDislikesHandler = async (id, btn) => {
        dislikesToggle = !dislikesToggle
        let post = await (await axios.get(`/posts-api/posts/${id}/`)).data

        // console.log(dislikesToggle)
        dislikesToggle ? 
        await axios.patch(`/posts-api/posts/${id}/`, {dislikes: post.dislikes + 1}):
        await axios.patch(`/posts-api/posts/${id}/`, {dislikes: post.dislikes - 1})

        dislikesToggle ?
        btn.children[0].style.color = 'red':
        btn.children[0].style.color = 'black'
        
    }


    return (
        <div className="container">
            <h1 className="text-left mb-4 mt-4">Social Posts</h1>

            {posts.map(post => (
                <div className="container" key={post.id}>
                    <div className="card mb-3" style={{width: '100%'}}>
                        <div className="card-body">
                            <p className="lead card-title text-left">{post.title}</p>
                            <p className="card-text text-left">{post.body}</p>
                            {/* <p className="card-text">By: {post.by}</p> */}
                            <p className="card-text text-left">Created at: {post.created_at}</p>
                            <button className="btn" onClick={(e) => setLikesHandler(post.id, e.target)}> <i className="far fa-thumbs-up"></i> </button> <span>{post.likes}</span>
                            <button className="btn" onClick={(e) => setDislikesHandler(post.id, e.target)}> <i className="far fa-thumbs-down"></i> </button> <span>{post.dislikes}</span>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Posts
