import React from 'react'
import { Link } from 'react-router-dom'

function Navbar() {
    return (
        <nav class="navbar navbar-expand-lg navbar-light bg-light">
            <div class="container-fluid">
                <a class="navbar-brand" href="#">ИП Иванов 181-321</a>
                {/* бургер на мобилке можно заменить на дроп дайун(асуждаю) меню */}
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarNavAltMarkup" >
                    <div class="navbar-nav">
                        <Link className="nav-link" to={{pathname: `/posts/`, fromDashboard: false}}>Посты</Link>
                        <Link className="nav-link" to={{pathname: `/music/`, fromDashboard: false}}>Музыка</Link>
                        <Link className="nav-link" to={{pathname: `/videos/`, fromDashboard: false}}>Видео</Link>
                        {/* <a class="nav-link active" aria-current="page" href="#">Посты</a>
                        <a class="nav-link" href="#">Музыка</a>
                        <a class="nav-link" href="#">Видео</a> */}
                        {/* <a class="nav-link disabled" href="#" tabindex="-1" aria-disabled="true">Disabled</a> */}
                    </div>
                </div>
            </div>

            <span>привет,</span>  &nbsp;&nbsp; <strong>username</strong>

            <a class="btn btn-primary" style={{marginRight: '30px', marginLeft: '30px'}} data-bs-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample">
                <i class="fas fa-cog"></i>
            </a>

            <div class="collapse" id="collapseExample" style={{position: 'fixed', top: '40px', right: '20px'}}>
                <div class="card card-body">
                    <p>
                        <a href="http://127.0.0.1:8000/auth/login/">Войти</a>
                    </p>
                    <p>
                        <a href="http://127.0.0.1:8000/api-auth/logout/">Выйти из аккаунта</a>
                    </p>
                    <p>
                        <a href="http://127.0.0.1:8000/auth/lk/">Личный кабинет</a>
                    </p>
                    <p>
                        <a href="http://127.0.0.1:8000/admin">Панель админа</a>
                    </p>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
