import React from 'react'
import DjangoCSRFToken from 'django-react-csrftoken'
import { useEffect, useState } from 'react'
import axios from 'axios'


function LogIn() {

    const [username, setUsername] = useState()
    const [password, setPassword] = useState()

    const Submit = () => {
        axios.post('/api-auth/login/', { username: username, password: password})
    }



    return (
        <div className="container" style={{marginTop: '50px'}}> 
            <a className="btn btn-secondary mb-4" href="/auth/registration">Dont have account?</a>

            <h2 className="mb-4">LogIn</h2>

            <form action='/api-auth/login/' method="POST">
                <DjangoCSRFToken />
                <div className="mb-3">
                    <label for="exampleInputEmail1" className="form-label">Username</label>
                    <input type="text" onChange={(e) => { setUsername(e.target.value) }} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" name="username" />
                </div>
                <div className="mb-3">
                    <label for="exampleInputPassword1" className="form-label">Password</label>
                    <input type="password" onChange={(e) => { setPassword(e.target.value) }} className="form-control" id="exampleInputPassword1" name="password" />
                </div>
                <button onClick={Submit} className="btn btn-primary">Log In</button>
            </form>
        </div>
    )
}

export default LogIn
